  async api_login(data: any) {
    this.authservice.con(data, 'login').then(async (res) => {
      this.data = JSON.parse(String(res).toString());
      console.log(this.data);
      if (this.data.error === false) {
        console.log('Login Successfully');
        this.toast.loginSuccessfully();
        this.route.navigate(['home']);
        console.log(this.data);
        return;
      }
      else {
        console.log('invalid email or pasword');
        this.toast.logininvalid();

      }
    }, (err) => {
      console.log(err);
    });
  }

  async api_getPlots() {
    await this.authservice.getdata('getPlots').then((result) => {
      this.data = JSON.parse(String(result));
      console.log(this.data);
      console.log(this.data.filter(x => x.type === 'Commercial').length)
      console.log(this.data.filter(x => x.type === 'Residential').length)
      this.global.set_getplots(this.data);
      // luqman twon only
      this.global.store_LQ_only(this.data.filter(x => x.plot === 'Baba Fareed'))
      // luqman twon total plots
      this.global.store_LQ_length(this.data.filter(x => x.plot === 'Baba Fareed').length)
      // luqman twon total Commercial plots
      this.global.store_LQC_length(this.data.filter(x => (x.type === 'Commercial') && (x.plot == 'Baba Fareed')).length)
      // luqman twon total Residential plots
      this.global.store_LQR_length(this.data.filter(x => (x.type === 'Residential') && (x.plot == 'Baba Fareed')).length)
      // luqman twon total commercial booked plots
      this.global.store_LQCB_length(this.data.filter(x => (x.status === 'booked') && (x.type === 'Commercial') && (x.plot == 'Baba Fareed')).length)
      // luqman twon total commercial for sale plots
      this.global.store_LQCS_length(this.data.filter(x => (x.status === 'for sale') && (x.type === 'Commercial') && (x.plot == 'Baba Fareed')).length)
      // luqman twon total residential booked plots
      this.global.store_LQRB_length(this.data.filter(x => (x.status === 'booked') && (x.type === 'Residential') && (x.plot == 'Baba Fareed')).length)
      // luqman twon total residentail booked plots
      this.global.store_LQRS_length(this.data.filter(x => (x.status === 'for sale') && (x.type === 'Residential') && (x.plot == 'Baba Fareed')).length)
       // luqman twon total booked plots
      this.global.store_lqbooked_length(this.data.filter(x => (x.status === 'booked') && (x.plot == 'Baba Fareed')).length)
       // luqman twon total for sale plots
      this.global.store_lqforsale_length(this.data.filter(x => (x.status === 'for sale') && (x.plot == 'Baba Fareed')).length)



      // 7star Only
      this.global.store_7Splots_Only(this.data.filter(x => x.plot === '7 Star'))
      // 7star total plots
      this.global.store_7S_length(this.data.filter(x => x.plot === '7 Star').length)
      // 7 star total commercial  plots
      this.global.store_7SC_length(this.data.filter(x => (x.type === 'Commercial') && (x.plot == '7 Star')).length)
      // 7 star total residentail  plots
      this.global.store_7SR_length(this.data.filter(x => (x.type === 'Residential') && (x.plot == '7 Star')).length)
      // 7 star total commercial booked plots
      this.global.store_7SCB_length(this.data.filter(x => (x.status === 'booked') && (x.type === 'Commercial') && (x.plot == '7 Star')).length)
      // 7 star total commercial for sale plots
      this.global.store_7SCS_length(this.data.filter(x => (x.status === 'for sale') && (x.type === 'Commercial') && (x.plot == '7 Star')).length)
      // 7 star total residentail booked plots
      this.global.store_7SRB_length(this.data.filter(x => (x.status === 'booked') && (x.type === 'Residential') && (x.plot == '7 Star')).length)
      // 7 star total residentail for sale plots
      this.global.store_7SRS_length(this.data.filter(x => (x.status === 'for sale') && (x.type === 'Residential') && (x.plot == '7 Star')).length)
             // 7 Star total booked plots
             this.global.store_7sbooked_length(this.data.filter(x => (x.status === 'booked') && (x.plot == '7 Star')).length)
             // 7 Star total for sale plots
            this.global.store_7sforsale_length(this.data.filter(x => (x.status === 'for sale') && (x.plot == '7 Star')).length)
    }, (err) => {
      console.log(err);
    });
  }


